import React, { useEffect } from 'react';
import Project from './Project';
import { useSelector, useDispatch } from 'react-redux';
import { onTouchMove, onTouchStart, onTouchEnd } from '../utils/touchHandler';
import { setFilter } from '../redux/actions';


const Landing = () => {
    const dispatch = useDispatch();
    const { filterElements, filter, projects } = useSelector(state => state.projectReducer);
    let row = filter / 3;
    let column = filter % 3;

    return (
        <section id='Projects'
            onTouchStart={touchStart => onTouchStart(touchStart)}
            onTouchMove={touchMoveEvent => onTouchMove(touchMoveEvent)}
            onTouchEnd={() => { onTouchEnd(dispatch, 50) }}>
            <div className='content'>
                <div className='heading'>
                    Self Projects
                </div>
                <div className='heading-end'>
                </div>
                <div className='filter'>

                    <div className='filterButton' />
                    <div className='filterElements'>
                        <div className='filterBox' style={
                            {
                                top: document.getElementById('filterElement' + filter) ? document.getElementById('filterElement' + filter).offsetTop : 16,
                                left: document.getElementById('filterElement' + filter) ? document.getElementById('filterElement' + filter).offsetLeft : 16
                            }
                        } />
                        {filterElements.map((ele, id) => (
                            <div className='filterElement' key={id} id={'filterElement' + id} style={filter === id ? { color: "white" } : {}} onClick={() => {
                                dispatch(setFilter(id));
                            }}>
                                {ele}
                            </div>
                        )
                        )}
                    </div>
                </div>
                <div className='projects'>
                    {getJsx(projects, filterElements[filter])}
                </div>
            </div>
        </section>
    );
}

const getJsx = (projects, filter) => {
    let op = [];
    Object.keys(projects).forEach((val) => {
        if (filter.indexOf('All') !== -1)
            op.push(<Project key={val} data={projects[val]} heading={val} />);
        else if(contains(projects[val].tags,filter)){
            op.push(<Project key={val} data={projects[val]} heading={val} />);
        }
    });
    return op;
}

const contains = (list,ele) => {
    if(ele === 'JavaScript')
        ele ='js';
    for(let element of list){
        
        if(element.toLowerCase().indexOf(ele.toLowerCase())!==-1)
            return true;
    }
    return false;
}
export default Landing;