import React from 'react';
const Project = ({ heading, data }) => {

    let x = {
        tags: ['js', 'HTML', 'CSS', 'ServiceWorker'],
        description: [
            `An offline to do list`,
            `Works by installing a service worker`,
            `Allows user to create,edit,update and delete entries`,
            `Update the entries in localstorage after every change.`,
            `Allow users to filter out entries based on active/completed status`,
            `Allow users to mark all entries as completed`,
            `Allow users to delete all completed entries`,
        ],
        github: 'http://github.com/rohanakar/todo',
        live: 'https://rohanakar.github.io/todo'
    }

    return (
        <section id='project'>

                <div className='img-container'>

                </div>
                <div className='img'>
                    <img src={'/Screenshot/' + heading + '/heading.jpg'} />
                </div>
        </section>
    )
}

export default Project;